.modalTemplate {
    width: 450px;
    padding: 15px 25px;
    border-radius: 20px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
    background: #ffffff;
    text-align: center;
    padding-top: 40px;
    -webkit-box-shadow: 0 3px 9px 0 rgba(0,0,0,0.36);
            box-shadow: 0 3px 9px 0 rgba(0,0,0,0.36);
}

.icon_close {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
}



